.info {
  background-color: $white;
  color: $gray;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  width: 61%;
  max-width: 1200px;
  margin: 0 auto;
}

.info__wrap {
  margin: 30px auto 110px;
  padding-left: 176px;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 220px;
    height: 317px;
    left: -34px;
    top: 35px;
    background-image: url("../../img/year-desk.png");
    background-repeat: no-repeat;
  }
}

.info__title {
  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  color: $gray;
  margin-bottom: 22px;
}

.info__text {
  font-family: "PT Mono", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $gray;
  padding-bottom: 5px;
}

.info__ellipsis {
  display: none;
}

.info__button {
  width: 180px;
  border: 1px solid $lightblue;
  box-sizing: border-box;
  border-radius: 5px;

  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $lightblue;
  background-color: $white;
  margin-top: 16px;
  padding: 14px;

  &:hover {
    background-color: $lightblue;
    color: $white;
    cursor: pointer;
  }
}


@media (max-width: $tablet-width) {

  .info {
    width: 93.75%;
    min-width: 750px;
  }

  .info__wrap {
    margin: 5px auto 83px;
    padding-left: 202px;

    &::before {
      width: 251px;
      height: 361px;
      left: -9px;
      top: 7px;
      background-image: url("../../img/year-tablet.png");
    }
  }

  .info__title {
    font-size: 32px;
    line-height: 45px;
    margin-bottom: 22px;
    margin-top: 10px;
  }

  .info__text {
    padding-right: 15px;
  }

  .info__ellipsis {
    display: inline;
  }

  .info__continuation {
    display: none;
  }

  .info__button {
    margin-top: 21px;
    padding: 14px;
  }
}

@media (max-width: $mobile-width) {

  .info {
    width: 90.6%;
    min-width: 320px;
  }

  .info__wrap {
    margin: 5px auto 64px;
    padding-left: 8px;

    &::before {
      display: none;
    }
  }

  .info__title {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 10px;
    margin-top: 20px;
  }

  .info__text {
    padding-top: 5px;
    padding-bottom: 19px;
    margin-bottom: 0;
  }

  .info__button {
    margin-top: 9px;
    padding: 14px;
  }
}
